span[data-v-5ce961c4]{
  display: inline-block;  /*用来设置行内元素的宽高*/
  width: 30px;
  height: 30px;
  text-align: center;  /*行内内容居中*/
  line-height: 32px;   /*多行文本的间距*/
  border-radius: 50%;  /*外边框圆角*/
  background: #f2b81c;
  color: #fff;
  text-shadow: 1px 0 1px #795c19;  /*为文字添加阴影*/
  font-weight: bold;
  text-transform: uppercase;  /*强制所有字符被转换为大写*/
  font-size: 18px;
  margin-top: 15px;
}

.modal-mask[data-v-37aed415] {
  position: fixed;
  /*元素被固定在页面上*/
  z-index: 100;
  /*当元素之间重叠的时候，z-index较大的元素会覆盖较小的元素在上层进行显示*/
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: table;
  -webkit-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
  /*过渡：属性 持续时间 函数 延迟时间*/
}
.modal-wrapper[data-v-37aed415] {
  display: table-cell;
  vertical-align: middle;
}
